import * as React from 'react';
import {observer} from 'mobx-react';

import { DialogStore } from '../stores/DialogStore';

@observer
export default class DialogView extends React.Component<{dialogStore: DialogStore}, any> {
  render() {
    return (
      <div aria-hidden="true" className="modal modal-va-middle fade" id="ui_dialog" role="dialog" tabIndex="-1">
        <div className="modal-dialog modal-xs">
          <div className="modal-content">
            <div className="modal-heading">
              <p className="modal-title">{this.props.dialogStore.title}</p>
            </div>
            <div className="modal-inner margin-bottom-sm">
              {this.props.dialogStore.childrenComponent}
            </div>
          </div>
        </div>
      </div>
    );
  }
}



