import * as React from 'react';
import {observer} from 'mobx-react';

import {CustomersList} from '../../stores/CustomerInfoStore';

@observer
export default class CustomerListView extends React.Component<{customersList: CustomersList}, any> {
  componentDidMount() {
    return this.props.customersList.getCustomers();
  }

  render() {
    return <div>
      <div className="container form-inline">
        <div className="form-group form-group-label margin-bottom-sm">
          <label className="floating-label" for="search">Search</label>
          <input className="form-control" id="search" type="text"/>
        </div>

        <button className="btn btn-brand" onClick={() => this.props.customersList.showCreateForm()}>Create</button>
      </div>
      <CustomersTableView customersList={this.props.customersList} />
    </div>;
  }
}

@observer
export class CustomersTableView extends React.Component<{customersList: CustomersList}, any> {
  render() {
    let customers = this.props.customersList.customers;
    return <div className="table-responsive">
        <table className="table">
          <thead>
            <tr>
              <th>NO</th>
              <th>Name</th>
              <th>Phone Number</th>
            </tr>
          </thead>
          <tbody>
            { customers.map((customer, index) => {
                return <tr onClick={() => this.props.customersList.showDetail(customer)}>
                  <td>{index+1}</td>
                  <td>{customer.name}</td>
                  <td>{customer.phoneNumber}</td>
                </tr>;
              })
            }
          </tbody>
        </table>
      </div>;
  }
}



