import * as React from 'react';
import {observer} from 'mobx-react';

import CustomerListView from './CustomerListView';
import TwilioStore from '../../stores/TwilioStore';
import {CustomersList} from '../../stores/CustomerInfoStore';
import snackbar from '../Snackbar';

let twilioStore = new TwilioStore();

let customersList = new CustomersList();

@observer
export default class MainPage extends React.Component<any, any> {

  render() {
    return <div className="">

      <div className="row">
        <div className="col-md-4">
          <CustomerListView customersList={customersList} />
        </div>
        <div className="col-md-6">
          <div className="container">
            { !customersList.newCustomer ?
              <CustomerDetailView customersList={customersList} /> :
              <NewCustomerView customersList={customersList} />
            }
          </div>
        </div>

        <div className="col-md-2">
          <CallControlView twilioStore={twilioStore} />
        </div>
      </div>
    </div>;
  }

}


@observer
class NewCustomerView extends React.Component<{customersList: CustomersList}, any> {
  onClickCreate = () => {
    this.props.customersList.createCustomer().then(() => {
      snackbar({content: 'Customer is created'});
    });
  }

  render() {
    let newCustomer = this.props.customersList.newCustomer;
    return (
      <div className="row">
        <div className="row">
          <div className="col-md-7">
            <div className="form-group form-group-label force-control-highlight">
              <label className="floating-label" for="no">No</label>
              <input className="form-control" id="no" type="text" />
            </div>

            <div className="form-group form-group-label force-control-highlight">
              <label className="floating-label" for="name"> Name </label>
              <input className="form-control" id="name" type="text" value={newCustomer.name} onChange={(e: any) => newCustomer.name = e.target.value} />
            </div>

            <div className="form-group form-group-label force-control-highlight">
              <label className="floating-label" for="phone-number"> Phone Number </label>
              <input className="form-control" id="phone-number" type="text" value={newCustomer.phoneNumber}
              onChange={(e: any) => newCustomer.phoneNumber = e.target.value} />
            </div>

            <div className="form-group form-group-label force-control-highlight">
              <label className="floating-label" for="email"> Email </label>
              <input className="form-control" id="email" type="email" value={newCustomer.email}
                onChange={(e: any) => newCustomer.email = e.target.value} />
            </div>

            <div className="form-group form-group-label  force-control-highlight">
              <label className="floating-label" for="sex"> Sex </label>
              <input className="form-control" id="sex" type="text" value={newCustomer.sex}
                onChange={(e: any) => newCustomer.sex = e.target.value} />
            </div>
          </div>


        </div>

        <div className="col-md-7">
          <button className="btn btn-brand pull-right" onClick={this.onClickCreate}>Create</button>
        </div>
      </div>
    );
  }
}


@observer
class CustomerDetailView extends React.Component<{customersList: CustomersList}, any> {
  render() {
    let detailedCustomer = this.props.customersList.detailedCustomer;
    return (
      <div className="row">
        <div className="row">
          <div className="col-md-7">
            <div className="form-group form-group-label force-control-highlight">
              <label className="floating-label" for="no">No</label>
              <input className="form-control" id="no" type="text" />
            </div>

            <div className="form-group form-group-label force-control-highlight">
              <label className="floating-label" for="name"> Name </label>
              <input className="form-control" id="name" type="text" value={detailedCustomer.name}/>
            </div>

            <div className="form-group form-group-label force-control-highlight">
              <label className="floating-label" for="phone-number"> Phone Number </label>
              <input className="form-control" id="phone-number" type="text" value={detailedCustomer.phoneNumber} />
            </div>

            <div className="form-group form-group-label force-control-highlight">
              <label className="floating-label" for="email"> Email </label>
              <input className="form-control" id="email" type="email" value={detailedCustomer.email} />
            </div>

            <div className="form-group form-group-label  force-control-highlight">
              <label className="floating-label" for="sex"> Sex </label>
              <input className="form-control" id="sex" type="text" value={detailedCustomer.sex} />
            </div>
          </div>

        </div>

        <div className="">
          <div className="form-group form-group-label">
            <label className="floating-label" for="note"> Note </label>
            <textarea className="form-control textarea-autosize" id="note" rows="4"></textarea>
          </div>
        </div>

        <div className="">
          <button className="btn btn-brand pull-right">Edit</button>
        </div>
      </div>
    );
  }
}

@observer
class CallControlView extends React.Component<{twilioStore: TwilioStore}, any> {
  render() {
    return (
      <div>
        <div aria-hidden="true" className="modal modal-va-middle fade" id="ui_incoming_dialog" role="dialog" tabIndex="-1">
          <div className="modal-dialog modal-xs">
            <div className="modal-content">
              <div className="modal-inner">
                <p className="h5">Incoming connection from {twilioStore.from}</p>
              </div>
              <div className="modal-footer">
                <p className="text-right">
                  <button className="btn btn-flat btn-brand-accent" data-dismiss="modal">Cancel</button>
                  <button className="btn btn-flat btn-brand-accent" data-dismiss="modal" onClick={() => twilioStore.accept()}>Receive</button>
                </p>
              </div>
            </div>
          </div>
        </div>

        <div className="margin-bottom">
          <button className="btn btn-green" onClick={() => twilioStore.setup()}>Start</button>
          <button className="btn btn-red">Exit</button>
        </div>

        <div className="">
          <div className="margin-bottom">
            <button className="btn btn-brand">Call</button>
          </div>
        </div>

        <div className="">
          <button className="btn btn-green">Transfer</button>
          <button className="btn btn-orange">Hold</button>
          <button className="btn btn-red" onClick={() => twilioStore.hangup()}>Hang Up</button>
        </div>
      </div>

    );
  }
}



